import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SessionsComponent } from '../views/sessions/sessions.component';
import { SessionsContainerComponent } from '../views/sessions-container/sessions-container.component';
import { sessionsDataResolver } from '../resolvers/sessions.resolver';

const routes: Routes = [
  {
    path: '',
    component: SessionsContainerComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: SessionsComponent,
        resolve: {
          sessions: sessionsDataResolver
        }
      },
    ]
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class SessionsRoutingModule { }
