import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Level } from '@app/shared/interfaces/level';
import { Session } from '@app/shared/interfaces/session';

@Component({
  selector: 'app-sessions',
  templateUrl: './sessions.component.html',
})
export class SessionsComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute
  ) {
  }

  levels:Level[] = [];
  sessions:Session[] = [];

  async ngOnInit() {
    const allSessionizeData = await this.activatedRoute.snapshot.data["sessions"];

    this.sessions = allSessionizeData.sessions;
  }
}
